@import url('https://fonts.googleapis.com/css?family=Amatic+SC:400,700|Josefin+Slab:400,600|Luckiest+Guy|Limelight|Pacifico|Poiret+One|Permanent+Marker|Ribeye|Poller+One|Sarina&display=swap');


article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

.wrapper {
    width: 95%;
    margin: 0 auto;
}

.header-container {
    display: flex;
    height: 98vh;
    position: relative;
    border: 8px solid black;
    justify-content: center;
}

.scroll-note {
    padding-left: 3%;
    font-family: 'Josefin Slab', serif;
    font-weight: 700;
    font-size: 1.6rem;
    letter-spacing: 1.5px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.scroll-note span {
    font-size: 1rem;
    color: #96D7C8;
}

.header {
    padding-top: 5%;
    width: 45%;
    margin-top: 8%;
    height: 65vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: 3%;
    transform: rotate(-15deg);
    font-size: 2.5rem;
}

.header img {
    width: 23%;
    padding-top: 3%;
    max-width: 100%;
}
.header-title {
    box-shadow: 10px 10px#96D7C8;
    border: 5px solid rgb(187, 187, 187);
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    padding-bottom: 5%;
    margin-bottom: 3%;
}

.header-title img {
    width: 80%;
    margin: 0 auto;
    padding-top: 0;
}

h1 {
    text-align: center;
    font-family: 'Luckiest Guy', cursive;
    margin-bottom: 0;
    margin-top: 5%;
    letter-spacing: 4px;
    text-shadow: 6px 6px  rgb(205, 205, 205);
    line-height: 1;
    display: flex;
    flex-direction: column;
}

h2 {
    font-family: 'Sarina', cursive;
    margin-top: 1%;
    font-size: 2.2rem;
    text-align: center;
}

.background-dog {
    width: 50%;
    height: 95vh;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    margin-left: -10%;
    padding-top: 1%;
}

.background-dog img {
    max-width: 100%;
    width: 85%;
    padding-left: 3%;
    margin-top: 50px;
    display: block;
}

.main-container {
    background-color: #F2F2F2;
    overflow: hidden;
}

.form-description {
    padding-top: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.form-description img {
    width: 30%;
}

.form-description p {
    font-family: 'Josefin Slab', serif;
    font-weight: 600;
    font-size: 1.5rem;
    margin: 0;
    margin-bottom: 10px;
    text-align: center;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3% 0;
}

.form input {
    height: 40px;
    width: 550px;
    text-align: center;
    font-size: 1.7rem;
    margin-bottom: 1%;
    font-family: 'Limelight', cursive;

}

.form button {
    width: 150px;
    font-size: 1.5rem;
    margin: 8px 5px;
    border-radius: 10px;
    font-family: 'Josefin Slab', serif;
    font-weight: 600;
}

.display {
    display: flex;
    background-color: #C3D7CE;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    border: 3px solid black;
    padding-top: 2%;
}

.movie p {
    font-family: 'Permanent Marker', cursive;
    font-size: 2rem; 
    max-width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
   
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.poster img {
    max-width: 100%;
    border: 6px solid black;
}

.movie {
    text-align: center;
    height: 760px;
    width: 400px;
    padding: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-bottom: 4%;

}

p.dog {
    font-family: 'Amatic SC', cursive;
    font-size: 2.2rem;
    font-weight: 600;
    color: red;
    padding-bottom: 0;
}

.scroll-here {
    font-family: 'Josefin Slab', serif;
    font-weight: 600;
    font-size: 1rem;
    text-align: center;
    text-transform: uppercase;
}



@media (max-width: 1100px) {
    .background-dog {
        justify-content: center;
        align-items: center
    }
}

